import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AESEncryptDecryptService, NotificationAlertService } from 'bd-common-lib';
import { Router, ActivatedRoute } from '@angular/router';
import { IndustryVerticalsService } from 'src/app/services/IndustryVerticals/IndustryVerticals.service';
import { HttpParams } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/app.constants';
import { GlobalVar } from 'src/app/shared/services/global-var';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-industry-verticals',
  templateUrl: './industry-verticals.component.html',
  styleUrls: ['./industry-verticals.component.css']
})
export class IndustryVerticalsComponent {
  productList: any[] = [];
  regionList: any[] = [];
  geoList: any[] = [];
  calanderList: any[] = [];
  industryVerticalsForm!: FormGroup;
  industryVerticalsData: any[] = [];
  templateError = '';
  GeoAll: boolean = false;
  hideURL:boolean=false;
constructor(
  public industryVerticalsService: IndustryVerticalsService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  public alert: NotificationAlertService) { }

ngOnInit(): void {
this.getProdDetails();
this.getRegionDetails();
this.getCalanderDetails();
this.createForm();
}

createForm() {
  this.industryVerticalsForm = this.fb.group({ 
    mcURL:[],
    mcProduct:[],
    mcGeography:[],
    mcFromMonth:[],
    mcToMonth:[],
    mcFromMonthC:[],
    mcToMonthC:[],
    mcSegmentType:[],
    mcDataMeasure:[],
    mcEvaluateU:[],
    mcWithPS:[],    
    mcCashCredit:[]
  });
}


myReportsFun(){ 

  var rtypevar=null;
  var rvaluevar=null;
  var rprodvar= null;
  var rapplid ="0002";
  var vcprodcode= this.industryVerticalsForm.get('mcProduct')?.value;  
  var mcsubprdcd= "";
  var mcpacktype= "";
  var tcprodcode= vcprodcode;
  this.templateError ='';
  var rtypevar=null;
  var rvaluevar=null;
  var rtype=null;
  var rvalue=null;

  if(vcprodcode != null && (vcprodcode=="T"||vcprodcode=="A"||vcprodcode=="C"||vcprodcode=="D"||vcprodcode=="E"||vcprodcode=="G"||vcprodcode=="I"||vcprodcode=="K"||vcprodcode=="L"||vcprodcode=="P"||vcprodcode=="V"))
      {	
        var var1 = vcprodcode.substring(0,1);
        var var2 = "T";
        var var3 = "T";      
        tcprodcode=var1;
        mcsubprdcd=var2;
        mcpacktype=var3; 
    }

    if(vcprodcode != null && (vcprodcode=="APC" || vcprodcode=="EPC"))
      {
          var var1 = vcprodcode.substring(0,1);
          var var2 = "PC";
          var var3 = "T";          
          tcprodcode=var1;
          mcsubprdcd=var2;
          mcpacktype=var3;           
      }
          
    if(vcprodcode != null && vcprodcode=="APCL" )
       {
          var var1 = vcprodcode.substring(0,1);
          var var2 = "PC";
          var var3 = "L";          
          tcprodcode=var1;
          mcsubprdcd=var2;
          mcpacktype=var3;           
       }
   
    let queryParams = new HttpParams();    
    queryParams = queryParams.append('puid',this.encrypt.decryptionAES(GlobalVar.EmpCode));
    queryParams = queryParams.append('papplid',rapplid);
    this.industryVerticalsService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {        
        this.geoList = res.result.geoAccessList;
      }
    })

    rtypevar = this.geoList[0];
    rvaluevar = this.geoList[1];
    rprodvar = this.geoList[2];

    if(rtypevar != null && (rtypevar=="R"||rtypevar=="B"||rtypevar=="A"||rtypevar=="S"))
      {
        rtype = rtypevar;
        rvalue = rvaluevar;
      }

  if (this.industryVerticalsForm.get('mcProduct')?.value !=null &&
  this.industryVerticalsForm.get('mcGeography')?.value !=null &&
  this.industryVerticalsForm.get('mcToMonth')?.value !=null &&
  this.industryVerticalsForm.get('mcToMonth')?.value !=null &&
  this.industryVerticalsForm.get('mcFromMonthC')?.value !=null &&
  this.industryVerticalsForm.get('mcToMonthC')?.value !=null &&
  this.industryVerticalsForm.get('mcDataMeasure')?.value !=null &&
  this.industryVerticalsForm.get('mcWithPS')?.value !=null &&
  this.industryVerticalsForm.get('mcEvaluateU')?.value !=null &&
  this.industryVerticalsForm.get('mcSegmentType')?.value !=null &&
  this.industryVerticalsForm.get('mcCashCredit')?.value !=null)
  {
    var puid=this.encrypt.decryptionAES(GlobalVar.EmpCode);
    let queryParams = new HttpParams();
    queryParams = queryParams.append('puid', puid);
    queryParams = queryParams.append('papplid', rapplid);
    this.industryVerticalsService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {
        this.geoList = res.result.geoAccessList;
        rtypevar = this.geoList[0].CGEOTYPE;
        rvaluevar = this.geoList[0].CGEOACCESS;
        console.log(this.geoList[0].CGEOTYPE);
        console.log(this.geoList[0].CGEOACCESS);      

  var ALLSTR = "&tUserCode="+ puid + //null
  "&tPrttype="+ this.geoList[0].CGEOTYPE + //null
  "&tPrvalue="+ this.industryVerticalsForm.get('mcGeography')?.value + //ALLCO
  "&tCprod="+ tcprodcode + //D
  "&tCsubprdcd="+ mcsubprdcd + //D
  "&tpCpacktype="+ mcpacktype + //T
  "&tfmthyr="+ this.industryVerticalsForm.get('mcFromMonth')?.value + //MAY2022
  "&tftmthyr="+ this.industryVerticalsForm.get('mcToMonth')?.value + //MAY2022
  "&tcmthyr="+ this.industryVerticalsForm.get('mcFromMonthC')?.value + //MAY2023
  "&tctmthyr="+ this.industryVerticalsForm.get('mcToMonthC')?.value +  //MAY2023
  "&ttorder="+ this.industryVerticalsForm.get('mcEvaluateU')?.value +  //G
  "&tfsflg="+ this.industryVerticalsForm.get('mcWithPS')?.value + //
  "&tSegmnt="+ this.industryVerticalsForm.get('mcSegmentType')?.value + //  
  "&tDataflg="+ this.industryVerticalsForm.get('mcDataMeasure')?.value + //R
  "&tPcshcr="+ this.industryVerticalsForm.get('mcCashCredit')?.value;

   var baseUrl =this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/Indvert_sum.rptdesign' + ALLSTR ;
   this.industryVerticalsForm.patchValue({
    mcURL: baseUrl
  });
   window.open(baseUrl,'_blank');
  }});
  }
  else
  {
    this.templateError = "Please Enter/Select All required fileds";
  }
}


getProdDetails() {
{
  let queryParams = new HttpParams();
  queryParams = queryParams.append('pprodvar','');
  this.industryVerticalsService.getAllProductDetails(queryParams).subscribe((res: any) => {
    if (res.result) {
      this.productList = res.result.list;

      this.industryVerticalsForm.patchValue({
        
        mcProduct:'T',
        mcFromMonth:formatDate(new Date, 'MMMyyyy', 'en-US').toUpperCase(),
        mcToMonth:formatDate(new Date, 'MMMyyyy', 'en-US').toUpperCase(),
        mcFromMonthC:formatDate(new Date(new Date().getFullYear()-1, new Date().getMonth(), 1), 'MMMyyyy', 'en-US').toUpperCase(),
        mcToMonthC:formatDate(new Date(new Date().getFullYear()-1, new Date().getMonth(), 1), 'MMMyyyy', 'en-US').toUpperCase(),
        mcSegmentType:'I',
        mcDataMeasure:'R',
        mcEvaluateU:'T',
        mcWithPS:'Y',    
        mcCashCredit:'T'
      });
    }
  })      
}
}

getRegionDetails() {
  {
    var puid=this.encrypt.decryptionAES(GlobalVar.EmpCode);
    var papplid = "0002";
    let queryParams = new HttpParams();
    queryParams = queryParams.append('puid', puid);
    queryParams = queryParams.append('papplid', papplid);
    this.industryVerticalsService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {
        this.geoList = res.result.geoAccessList;
        let queryParams = new HttpParams();
        queryParams = queryParams.append('prtype', this.geoList[0].CGEOTYPE);
        queryParams = queryParams.append('prvalue', this.geoList[0].CGEOACCESS);
        this.industryVerticalsService.getAllRegionDetails(queryParams).subscribe((res: any) => {
          if (res.result) {
            if (this.geoList[0].CGEOACCESS == "ALLCO") {
              this.regionList = res.result.list;
              this.GeoAll = true;
              this.industryVerticalsForm.patchValue({
                mcGeography: res.result.list[0].CVALUE
              });
            }
            else {
              this.GeoAll = false;
              this.regionList = res.result.list;
              console.log(res.result.list);
              this.industryVerticalsForm.patchValue({
                mcGeography: res.result.list[0].CVALUE
              });
            }
          }
        })
      }
    });
  }
}


getCalanderDetails() {
{
  let queryParams = new HttpParams();    
  queryParams = queryParams.append('puid','');
  this.industryVerticalsService.getAllCalanderDetails(queryParams).subscribe((res: any) => {
    if (res.result) {        
      this.calanderList = res.result.list;
    }
  })      
}
}

}
