constructor(
  public marketingReportsService: MarketingReportsService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  public alert: NotificationAlertService) { }

ngOnInit(): void {
this.getMarketingDetails();
this.createForm();
this.DATEshow=false;
this.Monthshow=false;
this.Yearshow=false;
}

createForm() {
  this.marketingReportsForm = this.fb.group({ 
    mcURL:[],
    mcReport:[''],
    mcMonth:['01'],
    mcYear:[this.date.getFullYear()]
  });
}

toggleReporttype(item: any) {  
  if (item.target.value=="0: CROSS" || item.target.value=="1: CUSTM" 
  || item.target.value=="10: ESA" 
  || item.target.value=="11: RETAIL"
  || item.target.value=="3: EXPRESS")
  {
    this.DATEshow=false;
    this.Monthshow=true;
    this.Yearshow=true;
  }
  else{
    this.DATEshow=false;
    this.Monthshow=false;
    this.Yearshow=false;
  }
}

getMarketingDetails() {
{
  this.marketingReportsService.getAllMarketingDetails().subscribe((res: any) => {
    if (res.result) {
      this.marketingList = res.result.list;
      this.marketingReportsForm.patchValue({
        mcReport:'6: AUTO'
      });
    }
  })      
}
}

