import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AESEncryptDecryptService, NotificationAlertService } from 'bd-common-lib';
import { Router } from '@angular/router';
import { ShippingPatternService } from 'src/app/services/ShippingPattern/ShippingPattern.service';
import { HttpParams } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/app.constants';
import { GlobalVar } from 'src/app/shared/services/global-var';

@Component({
  selector: 'app-shipping-pattern',
  templateUrl: './shipping-pattern.component.html',
  styleUrls: ['./shipping-pattern.component.css']
})
export class ShippingPatternComponent {
  productList: any[] = [];
  calanderList: any[] = [];
  geoList: any[] = [];
  shippingPatternForm!: FormGroup;
  templateError = '';
  hideURL:boolean=false;
constructor(
  public shippingPatternService: ShippingPatternService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  public alert: NotificationAlertService,
  public route: Router) {  }

ngOnInit(): void {
this.createForm();
this.getProdDetails();
this.getCalanderDetails();
}

createForm() {
this.shippingPatternForm = this.fb.group({ 
  mcURL:[],
  mcProduct:[],
  mcFromMonth:[]
});
}

myReportsFun(){ 
this.templateError ='';
var tcprodcode= this.shippingPatternForm.get('mcProduct')?.value;
var vcprodcode = this.shippingPatternForm.get('mcProduct')?.value;
var mcsubprdcd =this.shippingPatternForm.get('mcEvaluateU')?.value;
var mcpacktype='';
var rtypevar=null;
var rvaluevar=null;
var rprodvar= null;
var rapplid ='0002'; 
var rtype= null;
var rvalue= null;

if(vcprodcode != null && (vcprodcode=="T"||vcprodcode=="A"||vcprodcode=="C"||vcprodcode=="D"||vcprodcode=="E"||vcprodcode=="G"||vcprodcode=="I"||vcprodcode=="K"||vcprodcode=="L"||vcprodcode=="P"||vcprodcode=="V"))
    {	
      var var1 = vcprodcode;
      var var2 = "T";
      var var3 = "T";      
      tcprodcode=var1;
      mcsubprdcd=var2;
      mcpacktype=var3; 
  }

  if(vcprodcode != null && (vcprodcode=="APC" || vcprodcode=="EPC"))
    {
        var var1 = vcprodcode;
        var var2 = "PC";
        var var3 = "T";          
        tcprodcode=var1;
        mcsubprdcd=var2;
        mcpacktype=var3;           
    }
        
  if(vcprodcode != null && vcprodcode=="APCL" )
     {
        var var1 = vcprodcode;
        var var2 = "PC";
        var var3 = "L";          
        tcprodcode=var1;
        mcsubprdcd=var2;
        mcpacktype=var3;           
     }
 
  let queryParams = new HttpParams();    
  queryParams = queryParams.append('puid',this.encrypt.decryptionAES(GlobalVar.EmpCode));
  queryParams = queryParams.append('papplid',rapplid);
  this.shippingPatternService.getAllGeoDetails(queryParams).subscribe((res: any) => {
    if (res.result) {        
      this.geoList = res.result.geoAccessList;
    }
  })

  rtypevar = this.geoList[0];
  rvaluevar = this.geoList[1];
  rprodvar = this.geoList[2];

  if(rtypevar != null && (rtypevar=="R"||rtypevar=="B"||rtypevar=="A"||rtypevar=="S"))
    {
      rtype = rtypevar;
      rvalue = rvaluevar;
    }

if (this.shippingPatternForm.get('mcProduct')?.value !=null)
{
  var ALLSTR = "&tPprod="+ rtype + 
  "&tMont="+ rvalue ;

  ///Put correct URL after Client provide
  var baseUrl =this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/Negvar_sum.rptdesign' + ALLSTR ;
  this.shippingPatternForm.patchValue({
    mcURL: baseUrl
  });
  window.open(baseUrl,'_blank');
  }
  else
  {
    this.templateError = "Please Enter/Select All required fileds";
  }
}


getCalanderDetails() {
  {
    let queryParams = new HttpParams();    
    queryParams = queryParams.append('puid','');
    this.shippingPatternService.getAllCalanderDetails(queryParams).subscribe((res: any) => {
      if (res.result) {        
        this.calanderList = res.result.list;
      }
    })      
  }
}

getProdDetails() {
{
  let queryParams = new HttpParams();
  queryParams = queryParams.append('pprodvar','');
  this.shippingPatternService.getAllProductDetails(queryParams).subscribe((res: any) => {
    if (res.result) {
      this.productList = res.result.list;
    }
  })      
}
}

}
