import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/shared/app.constants';

@Injectable({
  providedIn: 'root'
})
export class EtailTrackerService {
  constructor(public httpClient: HttpClient) { }
     
  public getAllRegionDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.REGION_DATA,{params:body});
    } 
    public getAllGeoDetails(body: any): Observable<any> {
      return this.httpClient.get<any>(AppConstants.GETGEO_DATA,{params:body});
      }

      public getCustGroupDetails(body: any): Observable<any> {
        return this.httpClient.get<any>(AppConstants.CUSTGROUP_DATA,{params:body});
        }
 }
