import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/shared/app.constants';

@Injectable({
  providedIn: 'root'
})
export class SalesService {
  constructor(public httpClient: HttpClient) { }

  public getAllProductDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.PRODUCT_DATA, { params: body });
  }
  public getAllCalanderDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.CALANDER_DATA, { params: body });
  }
  public getAllGeoDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.GETGEO_DATA, { params: body });
  }
  public getAllCustGeoDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.GETCUSTGEO_DATA, { params: body });
  }
  public getCustGroupDetails(body: any): Observable<any> {
    return this.httpClient.get<any>(AppConstants.CUSTGROUP_DATA, { params: body });
  }

}
