import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CallbridgeComponent } from './components/callbridge/callbridge.component';
import { ChannelSalesReportComponent } from './components/channel-sales-report/channel-sales-report.component';
import { CockpitSummaryComponent } from './components/cockpit-summary/cockpit-summary.component';
import { CustVarianceComponent } from './components/cust-variance/cust-variance.component';
import { CustomerCentricComponent } from './components/customer-centric/customer-centric.component';
import { DhldgfReportComponent } from './components/dhldgf-report/dhldgf-report.component';
import { EdpReportsComponent } from './components/edp-reports/edp-reports.component';
import { EtailTrackerComponent } from './components/etail-tracker/etail-tracker.component';
import { HomeComponent } from './components/home/home.component';
import { IndustryVerticalsComponent } from './components/industry-verticals/industry-verticals.component';
import { KamActualVsTargetComponent } from './components/kam-actual-vs-target/kam-actual-vs-target.component';
import { KamCustomerGrowthComponent } from './components/kam-customer-growth/kam-customer-growth.component';
import { KamRegionGrowthComponent } from './components/kam-region-growth/kam-region-growth.component';
import { MarketingReportsComponent } from './components/marketing-reports/marketing-reports.component';
import { ProductYieldReportComponent } from './components/product-yield-report/product-yield-report.component';
import { ITC_UndeliveredComponent } from './components/ITC_Undelivered/ITC_Undelivered.component';
import { MembersComponent } from './components/members/members.component';
import { NdrActionDueComponent } from './components/ndr-action-due/ndr-action-due.component';
import { OpsMboComponent } from './components/ops-mbo/ops-mbo.component';
import { LoggedOutComponent } from './shared/layout/logged-out/logged-out.component';
import { PageNotFoundComponent } from './shared/layout/page-not-found/page-not-found.component';
import { UnAuthorizedAccessComponent } from './shared/layout/un-authorized-access/un-authorized-access.component';
import { ShippingPatternComponent } from './components/shipping-pattern/shipping-pattern.component';
import { SalesComponent } from './components/sales/sales.component';
import { ServiceQualityComponent } from './components/service-quality/service-quality.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'un-authorized-access', component: UnAuthorizedAccessComponent },
  { path: 'logged-out', component: LoggedOutComponent },
  { path: 'home', component: HomeComponent },
  { path: 'cust-variance', component: CustVarianceComponent },
  { path: 'industry-verticals', component: IndustryVerticalsComponent },
  { path: 'ops-mbo', component: OpsMboComponent },
  { path: 'call-Bridge', component: CallbridgeComponent },
  { path: 'dhldgf-report', component: DhldgfReportComponent },
  { path: 'marketing-reports', component: MarketingReportsComponent },
  { path: 'etail-tracker', component: EtailTrackerComponent },
  { path: 'cockpit-summary', component: CockpitSummaryComponent },
  { path: 'channel-sales-report', component: ChannelSalesReportComponent },
  { path: 'kam-customer-growth', component: KamCustomerGrowthComponent },
  { path: 'kam-region-growth', component: KamRegionGrowthComponent },
  { path: 'kam-actualsvstarget', component: KamActualVsTargetComponent },
  { path: 'product-yield-report', component: ProductYieldReportComponent },
  { path: 'ITC_Undelivered', component: ITC_UndeliveredComponent },  
  { path: 'members', component: MembersComponent },
  { path: 'ndr-action-due', component: NdrActionDueComponent },
  { path: 'edp-reports', component: EdpReportsComponent },
  { path: 'customer-centric', component: CustomerCentricComponent },
  { path: 'shipping-pattern', component: ShippingPatternComponent },
  { path: 'sales', component: SalesComponent },
  { path: 'service-quality', component: ServiceQualityComponent },
  { path: "**", component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
