import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AESEncryptDecryptService, NotificationAlertService } from 'bd-common-lib';
import { Router, ActivatedRoute } from '@angular/router';
import { OPSMBOService } from 'src/app/services/OPSMBO/OPSMBO.service';
import { HttpParams } from '@angular/common/http';
import { formatDate } from '@angular/common';
import { AppConstants } from 'src/app/shared/app.constants';
import { GlobalVar } from 'src/app/shared/services/global-var';

@Component({
  selector: 'app-ops-mbo',
  templateUrl: './ops-mbo.component.html',
  styleUrls: ['./ops-mbo.component.css']
})
export class OpsMboComponent {
  regionList: any[] = [];
  oPSMBOForm!: FormGroup;
  geoList: any[] = [];
  oPSMBOData: any[] = [];
  templateError = '';
  GeoAll:boolean=false;
  hideURL:boolean=false;
constructor(
  public oPSMBOService: OPSMBOService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  public alert: NotificationAlertService) { }

ngOnInit(): void {
this.getRegionDetails();
this.createForm();
}


getRegionDetails() {
  {
    var puid=this.encrypt.decryptionAES(GlobalVar.EmpCode);
    var papplid = "0002";
    let queryParams = new HttpParams();
    queryParams = queryParams.append('puid', puid);
    queryParams = queryParams.append('papplid', papplid);
    this.oPSMBOService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {
        this.geoList = res.result.geoAccessList;
        let queryParams = new HttpParams();
        queryParams = queryParams.append('prtype', this.geoList[0].CGEOTYPE);
        queryParams = queryParams.append('prvalue', this.geoList[0].CGEOACCESS);
        this.oPSMBOService.getAllRegionDetails(queryParams).subscribe((res: any) => {
          if (res.result) {
            if (this.geoList[0].CGEOACCESS == "ALLCO") {
              this.regionList = res.result.list;
              this.GeoAll = true;
              this.oPSMBOForm.patchValue({
                mcGeography: res.result.list[0].CVALUE
              });
            }
            else {
              this.GeoAll = false;
              this.regionList = res.result.list;
              console.log(res.result.list);
              this.oPSMBOForm.patchValue({
                mcGeography: res.result.list[0].CVALUE
              });
            }
          }
        })
      }
    });
  }
}



createForm() {
  this.oPSMBOForm = this.fb.group({ 
    mcURL:[],
    mcStatusCode:['033'],
    mcReportDate:[],
    mcGeography:[]
  });
}


myReportsFun(){ 

  var rtypevar=null;
  var rvaluevar=null;
  var rprodvar= null;
  var rapplid ="0002";
  var vcprodcode= this.oPSMBOForm.get('vcprodcode')?.value == undefined ? null : this.oPSMBOForm.get('vcprodcode')?.value;;
  var mcreptype= this.oPSMBOForm.get('mcreptype')?.value == undefined ? null : this.oPSMBOForm.get('mcreptype')?.value;;
  var rtype= this.oPSMBOForm.get('rtype')?.value == undefined ? null : this.oPSMBOForm.get('rtype')?.value;;
  var rvalue= this.oPSMBOForm.get('rvalue')?.value == undefined ? null : this.oPSMBOForm.get('rvalue')?.value;;
  var mtPeriod= this.oPSMBOForm.get('mtPeriod')?.value == undefined ? null : this.oPSMBOForm.get('mtPeriod')?.value;;  
  var mcsubprdcd= "";
  var mcpacktype= "";
  var tcprodcode= this.oPSMBOForm.get('vcprodcode')?.value == undefined ? null : this.oPSMBOForm.get('vcprodcode')?.value;;
  this.templateError ='';
  var mcStatusCode =this.oPSMBOForm.get('mcStatusCode')?.value;  
  var rtype= null;
  var rvalue= null;

  
  if(vcprodcode != null && (vcprodcode=="T"||vcprodcode=="A"||vcprodcode=="C"||vcprodcode=="D"||vcprodcode=="E"||vcprodcode=="G"||vcprodcode=="I"||vcprodcode=="K"||vcprodcode=="L"||vcprodcode=="P"||vcprodcode=="V"))
      {	
        var var1 = vcprodcode.substring(0,1);
        var var2 = "T";
        var var3 = "T";      
        tcprodcode=var1;
        mcsubprdcd=var2;
        mcpacktype=var3; 
    }

    if(vcprodcode != null && (vcprodcode=="APC" || vcprodcode=="EPC"))
      {
          var var1 = vcprodcode.substring(0,1);
          var var2 = "PC";
          var var3 = "T";          
          tcprodcode=var1;
          mcsubprdcd=var2;
          mcpacktype=var3;           
      }
          
    if(vcprodcode != null && vcprodcode=="APCL" )
       {
          var var1 = vcprodcode.substring(0,1);
          var var2 = "PC";
          var var3 = "L";          
          tcprodcode=var1;
          mcsubprdcd=var2;
          mcpacktype=var3;           
       }

    let queryParams = new HttpParams();    
    queryParams = queryParams.append('puid',this.encrypt.decryptionAES(GlobalVar.EmpCode));
    queryParams = queryParams.append('papplid',rapplid);
    this.oPSMBOService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {        
        this.geoList = res.result.geoAccessList;
      }
    })
    rtypevar = this.geoList[0];
    rvaluevar = this.geoList[1];
    rprodvar = this.geoList[2];

    if(rtypevar != null && (rtypevar=="R"||rtypevar=="B"||rtypevar=="A"||rtypevar=="S"))
      {
        rtype = rtypevar;
        rvalue = rvaluevar;
      }

  if (this.oPSMBOForm.get('mcStatusCode')?.value !=null &&
  this.oPSMBOForm.get('mcReportDate')?.value !=null &&
  this.oPSMBOForm.get('mcGeography')?.value !=null)
  {
  var ALLSTR =
  "&tDate="+ formatDate(this.oPSMBOForm.get('mcReportDate')?.value, 'dd-MMM-yy', 'en-US').toUpperCase() +
  "&rValue="+ this.oPSMBOForm.get('mcGeography')?.value +
  "&tCstatCode="+ this.oPSMBOForm.get('mcStatusCode')?.value ;

   var baseUrl =this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/exotel/OPSMBO.rptdesign' + ALLSTR ;
   this.oPSMBOForm.patchValue({
    mcURL: baseUrl
  });
   window.open(baseUrl,'_blank');
  }
  else
  {
    this.templateError = "Please Enter/Select All required fileds";
  }
}

}
