import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { NotificationAlertService,AESEncryptDecryptService } from 'bd-common-lib';
import { Router, ActivatedRoute } from '@angular/router';
import { DHLDGFReportService } from 'src/app/services/DHLDGFReport/DHLDGFReport.service';
import { HttpParams } from '@angular/common/http';
import {formatDate} from '@angular/common';

@Component({
  selector: 'app-dhldgf-report',
  templateUrl: './dhldgf-report.component.html',
  styleUrls: ['./dhldgf-report.component.css']
})
export class DhldgfReportComponent {
  date = new Date(); 
  year = this.date.getFullYear();
  month = this.date.getMonth();
  CurrDayMinus1 = formatDate(new Date(new Date().setDate(new Date().getDate()-1)), 'dd-MMM-yyyy', 'en-US');
  CurrMonth1Day = formatDate(new Date(this.year, this.month, 1), 'dd-MMM-yyyy', 'en-US');
  dHLDGFReportForm!: FormGroup;
  templateError = '';
  mcFromDate='';
  hideURL:boolean=false;
constructor(
  public dHLDGFReport: DHLDGFReportService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  public alert: NotificationAlertService) { }

ngOnInit(): void {
  this.date = new Date(); 
  this.year = this.date.getFullYear();
  this.month = this.date.getMonth();
  this.CurrDayMinus1 = formatDate(new Date(new Date().setDate(new Date().getDate()-1)), 'dd-MMM-yyyy', 'en-US');
  this.CurrMonth1Day = formatDate(new Date(this.year, this.month, 1), 'dd-MMM-yyyy', 'en-US');
  this.createForm(this.CurrDayMinus1,this.CurrMonth1Day);
  }
 
  createForm(CurrDayMinus1 : string ,CurrMonth1Day : string) {
    this.dHLDGFReportForm = this.fb.group({ 
      mcURL:[],
      //mcFromDate:CurrMonth1Day,
      //mcToDate:CurrDayMinus1
      mcFromDate:[''],
      mcToDate:['']
    });
  }

  
myReportsFun(){ 
  this.templateError ='';
  if (this.dHLDGFReportForm.get('mcFromDate')?.value !=null &&
  this.dHLDGFReportForm.get('mcToDate')?.value !=null)
  {
    var ALLSTR = 
    "&fromDT="+ formatDate(this.dHLDGFReportForm.get('mcFromDate')?.value, 'dd-MMM-yy', 'en-US').toUpperCase() + 
    "&ToDate="+ formatDate(this.dHLDGFReportForm.get('mcToDate')?.value, 'dd-MMM-yy', 'en-US').toUpperCase() ;
    var baseUrl =this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/dhlmis.rptdesign' + ALLSTR ;
    this.dHLDGFReportForm.patchValue({
      mcURL: baseUrl
    });
    window.open(baseUrl,'_blank'); 
  }
  else
  {
    this.templateError ='Please select from Date and To Date';
  } 
}
}
