import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup, FormControl } from '@angular/forms';
import { NotificationAlertService } from 'bd-common-lib';
import { Router } from '@angular/router';
import { CustVarianceService } from 'src/app/services/CustVariance/CustVariance.service';
import { HttpParams } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/app.constants';
import { AESEncryptDecryptService } from 'bd-common-lib';
import { GlobalVar } from 'src/app/shared/services/global-var';
import { formatDate } from '@angular/common';

@Component({
  selector: 'app-cust-variance',
  templateUrl: './cust-variance.component.html',
  styleUrls: ['./cust-variance.component.css']
})
export class CustVarianceComponent {
  productList: any[] = [];
  regionList: any[] = [];
  calanderList: any[] = [];
  geoList: any[] = [];
  custVarianceForm!: FormGroup;
  custVarianceData: any[] = [];
  templateError = '';
  dataitem: any[] = [];
  CollectionTypes: any[] = [];
  displayheaderResponseData: any = [];
  setSelectedProductList: string = "All Products";
  test: string = "";
  GeoAll: boolean = false;
  hideURL: boolean = false;
  rtypevarNew = "";
  rvaluevarNew = "";
  constructor(
    public custVarianceService: CustVarianceService,
    private fb: FormBuilder,
    public NofityService: NotificationAlertService,
    public route: Router,
    public encrypt: AESEncryptDecryptService
  ) { }


  ngOnInit(): void {
    this.createForm();
    this.getProdDetails();
    this.getRegionDetails();
    this.getCalanderDetails();
  }

  numberOnly(event: { which: any; keyCode: any; }): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57) || charCode == 109) {
      return false;
    }
    return true;
  }

  createForm() {
    this.custVarianceForm = this.fb.group({
      mcURL: [],
      mcProduct: [],
      mcGeography: [],
      mcFromMonth: [],
      mcToMonth: [],
      mcFromMonthC: [],
      mcToMonthC: [],
      mcDataMeasure: [],
      mcWithPS: [],
      mcEvaluateU: [],
      mcVolta: [],
      mcVariance: [],
      mcReportType: [],
      mcDisLimit: []
    });
  }

  myReportsFun() {
    var rtypevar = null;
    var rvaluevar = null;
    var rapplid = "0002";
    var mcsubprdcd = "";
    var mcpacktype = "";
    var vcprodcode = this.custVarianceForm.get('mcProduct')?.value
    var tcprodcode = vcprodcode;
    var puid = this.encrypt.decryptionAES(GlobalVar.EmpCode);
    this.templateError = '';
    if (this.custVarianceForm.get('mcProduct')?.value != null &&
      this.custVarianceForm.get('mcGeography')?.value != null &&
      this.custVarianceForm.get('mcToMonth')?.value != null &&
      this.custVarianceForm.get('mcToMonth')?.value != null &&
      this.custVarianceForm.get('mcFromMonthC')?.value != null &&
      this.custVarianceForm.get('mcToMonthC')?.value != null &&
      this.custVarianceForm.get('mcDataMeasure')?.value != null &&
      this.custVarianceForm.get('mcWithPS')?.value != null &&
      this.custVarianceForm.get('mcEvaluateU')?.value != null &&
      this.custVarianceForm.get('mcVolta')?.value != null &&
      this.custVarianceForm.get('mcVariance')?.value != null &&
      this.custVarianceForm.get('mcReportType')?.value != null &&
      this.custVarianceForm.get('mcDisLimit')?.value != null
    ) {

      let queryParams = new HttpParams();
      queryParams = queryParams.append('puid', puid);
      queryParams = queryParams.append('papplid', rapplid);
      this.custVarianceService.getAllGeoDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.geoList = res.result.geoAccessList;
          rtypevar = this.geoList[0].CGEOTYPE;
          rvaluevar = this.geoList[0].CGEOACCESS;
          console.log(this.geoList[0].CGEOTYPE);
          console.log(this.geoList[0].CGEOACCESS);

          if (vcprodcode != null && (vcprodcode == "T" || vcprodcode == "A" || vcprodcode == "C" || vcprodcode == "D" || vcprodcode == "E" || vcprodcode == "G" || vcprodcode == "I" || vcprodcode == "K" || vcprodcode == "L" || vcprodcode == "P" || vcprodcode == "V")) {
            var var1 = vcprodcode.substring(0, 1);
            var var2 = "T";
            var var3 = "T";

            tcprodcode = var1;
            mcsubprdcd = var2;
            mcpacktype = var3;
          }

          if (vcprodcode != null && (vcprodcode == "APC" || vcprodcode == "EPC")) {
            var var1 = vcprodcode.substring(0, 1);
            var var2 = "PC";
            var var3 = "T";

            tcprodcode = var1;
            mcsubprdcd = var2;
            mcpacktype = var3;

          }

          if (vcprodcode != null && vcprodcode == "APCL") {
            var var1 = vcprodcode.substring(0, 1);
            var var2 = "PC";
            var var3 = "L";
            tcprodcode = var1;
            mcsubprdcd = var2;
            mcpacktype = var3;
          }
          console.log("rvaluevar value--",rvaluevar);
          console.log("Region selected value--",this.custVarianceForm.get('mcGeography')?.value);
          if (rvaluevar != this.custVarianceForm.get('mcGeography')?.value) {
            rtypevar="R";
            rvaluevar=this.custVarianceForm.get('mcGeography')?.value;
          }

          var ALLSTR = "&tprType=" + rtypevar +
            "&tprValue=" + rvaluevar +
            "&tPprod=" + tcprodcode +
            "&tfmthyr=" + this.custVarianceForm.get('mcFromMonth')?.value +
            "&tftmthyr=" + this.custVarianceForm.get('mcToMonth')?.value +
            "&tcmthyr=" + this.custVarianceForm.get('mcFromMonthC')?.value +
            "&tctmthyr=" + this.custVarianceForm.get('mcToMonthC')?.value +
            "&ttorder=" + this.custVarianceForm.get('mcEvaluateU')?.value +
            "&tvarlimit=" + this.custVarianceForm.get('mcVariance')?.value +
            "&tdlimit=" + this.custVarianceForm.get('mcDisLimit')?.value +
            "&tfsflg=" + this.custVarianceForm.get('mcWithPS')?.value +
            "&tdataflg=" + this.custVarianceForm.get('mcDataMeasure')?.value +
            "&tpCsubprdcd=" + mcsubprdcd +
            "&tpCpacktype=" + mcpacktype +
            "&tcustflg=" + '';
          console.log(ALLSTR);
          var baseUrl = "";

          if (this.custVarianceForm.get('mcReportType')?.value == "D") {
            var baseUrl = this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/Negvar_detail.rptdesign' + ALLSTR;
          }
          else {
            var baseUrl = this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/Negvar_sum.rptdesign' + ALLSTR;
          }
          this.custVarianceForm.patchValue({
            mcURL: baseUrl
          });
          window.open(baseUrl, '_blank');

        }
      });
    }
    else {
      this.templateError = "Please Enter/Select All required fileds";
    }
  }

  PostDetails() {
    if (this.custVarianceForm.value.mcProductType) {
      const payload = {
        mcProduct: this.custVarianceForm.value.mcProductType,
        mcGeography: this.custVarianceForm.value.mcGeography,
        mcFromMonth: this.custVarianceForm.value.mcFromMonth,
        mcToMonth: this.custVarianceForm.value.mcToMonth,
        mcDisLimit: this.custVarianceForm.value.mcDisLimit
      }


      let queryParams = new HttpParams();
      queryParams = queryParams.append('pctype', this.custVarianceForm.value.mcProductType ? 'A' : 'A');
      //queryParams = queryParams.append('pcleg',this.pendingConnectionFirstMileForm.value.mcLeg ? 'A' :'A');
      queryParams = queryParams.append("pcregion", this.custVarianceForm.value.mcRegion ? 'ALL' : 'ALL');
      this.custVarianceService.getAllProductDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.custVarianceData = res.result.geoDetailsList;
        }
      })
    }
  }

  getProdDetails() {
    {

      let queryParams = new HttpParams();
      queryParams = queryParams.append('pprodvar', '');
      this.custVarianceService.getAllProductDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.productList = res.result.list;
          this.custVarianceForm.patchValue({
            mcProduct: 'T',
            mcFromMonth: formatDate(new Date(new Date().getFullYear()-1, new Date().getMonth(), 1), 'MMMyyyy', 'en-US').toUpperCase(),
            mcToMonth: formatDate(new Date(new Date().getFullYear()-1, new Date().getMonth(), 1), 'MMMyyyy', 'en-US').toUpperCase(),
            mcFromMonthC: formatDate(new Date, 'MMMyyyy', 'en-US').toUpperCase(),
            mcToMonthC: formatDate(new Date, 'MMMyyyy', 'en-US').toUpperCase(),
            mcDataMeasure: 'R',
            mcWithPS: 'Y',
            mcEvaluateU: 'G',
            mcVolta: 'N',
            mcVariance: '-1',
            mcReportType: 'S',
            mcDisLimit: '100'
          });
        }
      })
    }
  }

  getRegionDetails() {
    {
      console.log("Before decryption--", GlobalVar.EmpCode);
      var puid = this.encrypt.decryptionAES(GlobalVar.EmpCode);
      console.log("After decryption--", puid);
      var papplid = "0002";
      let queryParams = new HttpParams();
      queryParams = queryParams.append('puid', puid);
      queryParams = queryParams.append('papplid', papplid);
      this.custVarianceService.getAllGeoDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.geoList = res.result.geoAccessList;
          let queryParams = new HttpParams();
          console.log(this.geoList[0].CGEOTYPE);
          queryParams = queryParams.append('prtype', this.geoList[0].CGEOTYPE);
          queryParams = queryParams.append('prvalue', this.geoList[0].CGEOACCESS);
          this.custVarianceService.getAllRegionDetails(queryParams).subscribe((res: any) => {
            if (res.result) {
              if (this.geoList[0].CGEOACCESS == "ALLCO") {
                this.regionList = res.result.list;
                this.GeoAll = true;
                this.custVarianceForm.patchValue({
                  mcGeography: res.result.list[0].CVALUE
                });
              }
              else {
                this.GeoAll = false;
                this.regionList = res.result.list;
                console.log(res.result.list);
                this.custVarianceForm.patchValue({
                  mcGeography: res.result.list[0].CVALUE
                });
              }
            }
          })
        }
      });
    }
  }

  getCalanderDetails() {
    {
      let queryParams = new HttpParams();
      queryParams = queryParams.append('puid', '');
      this.custVarianceService.getAllCalanderDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.calanderList = res.result.list;
        }
      })
    }
  }
}

