    EtailTrackerComponent,
    CockpitSummaryComponent,
    ChannelSalesReportComponent,
    EdpReportsComponent,
    KamCustomerGrowthComponent,
    KamRegionGrowthComponent,
    KamActualVsTargetComponent,
    ProductYieldReportComponent,
    ITC_UndeliveredComponent,
