import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AESEncryptDecryptService, NotificationAlertService } from 'bd-common-lib';
import { Router, ActivatedRoute } from '@angular/router';
import { KAMActualvsTargetService } from 'src/app/services/KAMActualvsTarget/KAMActualvsTarget.service';
import { HttpParams } from '@angular/common/http';
import { formatDate } from '@angular/common';
import { AppConstants } from 'src/app/shared/app.constants';
import { GlobalVar } from 'src/app/shared/services/global-var';

@Component({
  selector: 'app-kam-actual-vs-target',
  templateUrl: './kam-actual-vs-target.component.html',
  styleUrls: ['./kam-actual-vs-target.component.css']
})
export class KamActualVsTargetComponent {
  productList: any[] = [];
  geoList: any[] = [];
  kAMActualvsTargetForm!: FormGroup;
  templateError = '';
  date = new Date();
  hideURL:boolean=false;
  Curyear = this.date.getFullYear();
  Pryear = this.date.getFullYear() - 1;
  regionList: any[] = [];
  rtypevar = null;
  rvaluevar = null;
  rprodvar = null;
  rapplid = "0002";
  rtype = null;
  rvalue = null;

  constructor(
    public kAMActualvsTargetService: KAMActualvsTargetService,
    private fb: FormBuilder,
    public encrypt:AESEncryptDecryptService,
    public alert: NotificationAlertService) { }

  ngOnInit(): void {
    this.createForm();
    this.getRegionDetails();
  }

  createForm() {
    this.kAMActualvsTargetForm = this.fb.group({
      mcURL:[],
      mcRegion: []
    });
  }

  getRegionDetails() {
    {
      var puid = this.encrypt.decryptionAES(GlobalVar.EmpCode);
      var papplid = "0002";
      let queryParams = new HttpParams();
      queryParams = queryParams.append('puid', puid);
      queryParams = queryParams.append('papplid', papplid);
      this.kAMActualvsTargetService.getAllGeoDetails(queryParams).subscribe((res: any) => {
        if (res.result) {
          this.geoList = res.result.geoAccessList;
          let queryParams = new HttpParams();
          queryParams = queryParams.append('prtype', this.geoList[0].CGEOTYPE);
          queryParams = queryParams.append('prvalue', this.geoList[0].CGEOACCESS);
          this.kAMActualvsTargetService.getAllRegionDetails(queryParams).subscribe((res: any) => {
            if (res.result) {
              if (this.geoList[0].CGEOACCESS == "ALLCO") {
                this.regionList = res.result.list;
                this.kAMActualvsTargetForm.patchValue({
                  mcRegion: res.result.list[0].CVALUE
                });
              }
              else {
                this.regionList = [];
                this.regionList = res.result.list;
                console.log(res.result.list[0].CVALUE);
                this.kAMActualvsTargetForm.patchValue({
                  mcRegion: res.result.list[0].CVALUE
                });
              }
            }
          })
        }
      });
    }
  }

  myReportsFun() {
    if (this.kAMActualvsTargetForm.get('mcRegion')?.value != null) {

      var mcRegionNew="";
      if (this.kAMActualvsTargetForm.get('mcRegion')?.value=="ALLCO")
      {
        mcRegionNew="ALL";
      }
      else
      {
        mcRegionNew=this.kAMActualvsTargetForm.get('mcRegion')?.value;
      }

      var ALLSTR = "&rCregion=" + mcRegionNew;
      var baseUrl = this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/KAM_ACTVSTGT.rptdesign' + ALLSTR ;
      this.kAMActualvsTargetForm.patchValue({
        mcURL: baseUrl
      });
      window.open(baseUrl, '_blank');
    }
    else {
      this.templateError = "Please Select at least one Region from the list";
    }
  }
}
