import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AESEncryptDecryptService, NotificationAlertService } from 'bd-common-lib';
import { Router, ActivatedRoute } from '@angular/router';
import { MembersService } from 'src/app/services/Members/Members.service';
import { HttpParams } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/app.constants';
import { GlobalVar } from 'src/app/shared/services/global-var';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.css']
})
export class MembersComponent {
  productList: any[] = [];
  geoList:any[]=[];
  membersForm!: FormGroup;
  templateError = '';
  hideURL:boolean=false;
constructor(
  public membersService: MembersService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  public alert: NotificationAlertService) { }

ngOnInit(): void {
this.getProdDetails();
this.createForm();
}


createForm() {
  this.membersForm = this.fb.group({ 
    mcURL:[],
    mcProduct:[]
  });
  }
  
  myReportsFun(){ 
  this.templateError ='';
  var tcprodcode= this.membersForm.get('mcProduct')?.value;
  var vcprodcode = this.membersForm.get('mcProduct')?.value;
  var vcprodcode1= this.membersForm.get('mcProduct')?.value;
  var mcsubprdcd ='';
  var mcpacktype='';
  var rtypevar=null;
  var rvaluevar=null;
  var rprodvar= null;
  var rapplid ='0002'; 
  var rtype= null;
  var rvalue= null;
  var rcchannel=null;
  var rcbranch=null;
  var rcregion= null;
  var mcarea=null;
  var mccustcode=null;
  
  if(vcprodcode != null && (vcprodcode=="T"||vcprodcode=="A"||vcprodcode=="C"||vcprodcode=="D"||vcprodcode=="E"||vcprodcode=="G"||vcprodcode=="I"||vcprodcode=="K"||vcprodcode=="L"||vcprodcode=="P"||vcprodcode=="V"))
      {	
        var var1 = vcprodcode;
        var var2 = "T";
        var var3 = "T";      
        tcprodcode=var1;
        mcsubprdcd=var2;
        mcpacktype=var3; 
    }
  
    if(vcprodcode != null && (vcprodcode=="APC" || vcprodcode=="EPC"))
      {
          var var1 = vcprodcode;
          var var2 = "PC";
          var var3 = "T";          
          tcprodcode=var1;
          mcsubprdcd=var2;
          mcpacktype=var3;           
      }
          
    if(vcprodcode != null && vcprodcode=="APCL" )
       {
          var var1 = vcprodcode;
          var var2 = "PC";
          var var3 = "L";          
          tcprodcode=var1;
          mcsubprdcd=var2;
          mcpacktype=var3;           
       }
   

       if(vcprodcode != null && vcprodcode=="D")
       {
               vcprodcode1="DP";
       }
       if(vcprodcode != null && vcprodcode=="A")
       {	
               vcprodcode1="APXB2B";
       } 
             if(vcprodcode != null && vcprodcode=="APC")
       {	
               vcprodcode1="APXB2C";
       }  
             if(vcprodcode != null && vcprodcode=="APCL")
       {	
               vcprodcode1="DRTPLS";
       } 
             if(vcprodcode != null && vcprodcode=="E")
       {	
               vcprodcode1="SFCB2B";
       } 
             if(vcprodcode != null && vcprodcode=="EPC")
       {	
               vcprodcode1="SFCB2C";
       } 
             if(vcprodcode != null && vcprodcode=="I")
       {	
               vcprodcode1="INTL";
       } 
             if(vcprodcode != null && vcprodcode=="V")
       {	
               vcprodcode1="IMPEXP";
       } 
             if(vcprodcode != null && vcprodcode=="PLCY")
       {	
               vcprodcode1="ATA";
       } 
             if(vcprodcode != null && vcprodcode=="T")
       {	
               vcprodcode1="ALL";
       }

    let queryParams = new HttpParams();    
    queryParams = queryParams.append('puid',this.encrypt.decryptionAES(GlobalVar.EmpCode));
    queryParams = queryParams.append('papplid',rapplid);
    this.membersService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {        
        this.geoList = res.result.geoAccessList;
      }
    })
  
    rtypevar = this.geoList[0];
    rvaluevar = this.geoList[1];
    rprodvar = this.geoList[2];
  
    if(rtypevar != null && (rtypevar=="R"||rtypevar=="B"||rtypevar=="A"||rtypevar=="S"))
      {
        rtype = rtypevar;
        rvalue = rvaluevar;
      }
  
  if (this.membersForm.get('mcProduct')?.value !=null)
  {
    var ALLSTR = "&rType="+ rtype +
    "&rValue="+ rvalue +
    "&tsupergrcd="+ '' +
    "&tgrpcd="+ '' +
    "&tProdcode="+ vcprodcode1 +
    "&tArea="+ mcarea ;

    //Need to put correct URL
    var baseUrl =this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/salesumt.rptdesign' + ALLSTR ;
    this.membersForm.patchValue({
      mcURL: baseUrl
    });
    window.open(baseUrl,'_blank');
    }
    else
    {
      this.templateError = "Please Enter/Select All required fileds";
    }
  } 

getProdDetails() {
  {
    let queryParams = new HttpParams();
    queryParams = queryParams.append('pprodvar','');
    this.membersService.getAllProductDetails(queryParams).subscribe((res: any) => {
      if (res.result) {
        this.productList = res.result.list;
      }
    })      
  }
}
}
