import { Component,ElementRef, Renderer2, ViewChild } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AESEncryptDecryptService, NotificationAlertService } from 'bd-common-lib';
import { Router, ActivatedRoute } from '@angular/router';
import { SalesService } from 'src/app/services/Sales/Sales.service';
import { HttpParams } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/app.constants';
import { GlobalVar } from 'src/app/shared/services/global-var';

@Component({
  selector: 'app-ITC_Undelivered',
  templateUrl: './ITC_Undelivered.component.html',
  styleUrls: ['./ITC_Undelivered.component.css']
})

export class ITC_UndeliveredComponent {
  geoList:any[]=[];
  ITC_UndeliveredForm!: FormGroup;
  hideURL:boolean=false;

constructor(
  public salesService: SalesService, 
  private fb: FormBuilder,
  public encrypt:AESEncryptDecryptService,
  private renderer: Renderer2,
  public alert: NotificationAlertService) { }


ngOnInit(): void {
  this.createForm();
}
createForm() {
  this.ITC_UndeliveredForm = this.fb.group({ 
    mcURL:[]
  });
  }
    
  myReportsFun(){ 
    var rtypevar=null;
    var rvaluevar=null;
    var rapplid ="0002";
    let queryParams = new HttpParams();   
    queryParams = queryParams.append('puid',this.encrypt.decryptionAES(GlobalVar.EmpCode));     
    queryParams = queryParams.append('papplid',rapplid);
    this.salesService.getAllGeoDetails(queryParams).subscribe((res: any) => {
      if (res.result) {        
        this.geoList = res.result.geoAccessList;
          rtypevar = this.geoList[0].CGEOTYPE;
          rvaluevar = this.geoList[0].CGEOACCESS;
            var ALLSTR = "&tRtype="+ rtypevar +
            "&tRvalue="+ rvaluevar ;
            var baseUrl =this.encrypt.decryptionAES(sessionStorage.getItem('reporturl')) + '/Resources/UserData/reportuser/ITCUDSUM.rptdesign' + ALLSTR ;
            this.ITC_UndeliveredForm.patchValue({
              mcURL: baseUrl
            });
            window.open(baseUrl,'_blank');
          }              
        });
      }
}
